body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #202124;
  color: #bdc1c6;
}


.footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: space-between;
  
}

.contato-div{
  display: grid;
  align-items: center;
  margin-bottom: -10px;
}

a{
  text-decoration: none;
  color: #4e9dfd;
}

.copy-div{
  margin-bottom: 2px;
  position: fixed;
  bottom: 0%;
  left: 50%;
}

.social-div{
  display: grid;
  margin-right: 1rem;
}

.footer-list{
  display: inline-block;
}

li{
  list-style: none;
}

span{
  display: flex;
  padding: 1px;
  margin: 5px;
}